export const searchBookDb = async (searchedWord = '', checked = false, start = 1) => {
    let db;

    // console.log('ldnaslmdsl;ak',process.env.REACT_APP_SERVER);
    if(searchedWord != ''){
        if(checked){
            db = await axios.get(`https://port-0-reactproject-br-server-6w1j2alm4a7mi8.sel5.cloudtype.app/book?query=${searchedWord}&display=9&start=${start}`)
        }else{
            db = await axios.get(`https://port-0-reactproject-br-server-6w1j2alm4a7mi8.sel5.cloudtype.app/book_adv?d_titl=${searchedWord}&display=9&start=${start}`)
        }
    }else{
        return 'err';
    }

    return db;
}//searchBookDb() 


