export const searchYoutubeDb = async (bookName = '') => {
    if(bookName != ''){
        const youtubeDb = axios.create({
            baseURL:'https://www.googleapis.com/youtube/v3/search',
            params: {
                'key': 'AIzaSyBblyK8KTUyGb06f2ZckwaAHQUorysCJe0',
                'type': 'video',
                'part': 'snippet'
            }
        })

        let db = await youtubeDb.get('/', {params: {'q': `${bookName}  `}});
        return db;
    }
} //searchYoutubeDb() 

