import React, { useContext, useEffect, useState } from 'react';
import { Link } from 'react-router-dom';

import { MyContext } from './Context';


export default function MyList() {
  let {id1List, setId1List} = useContext(MyContext);
  
  const deleteListItem = (id) => {
    let imsiList = id1List.filter((obj)=>{
      if(obj.isbn != id) return obj
    })
  
    setId1List(imsiList);
  }//deleteListItem() 



  
  return (
    <div className='MyListCompo'>
      <h2>My Book List</h2>
      <ul>
        {
          id1List.map((obj, k)=>(
            <li key={k}>
              <button type='button' className='xBtn' onClick={()=>{deleteListItem(obj?.isbn)}}></button>

              <figure className='bookInfoWrapper'>
                <i>
                  <img src={obj?.img} alt={obj?.title}/>
                </i>

                <div className='right'>
                  <figcaption>{obj?.title}</figcaption>
                  <span className='author'>: <code>{obj?.author}</code></span>
                  <span className='publisher'>: <code>{obj?.publisher}</code></span>

                  <Link className="moveToDetail" to={`/Detail/${obj?.isbn}`}> </Link> 
                </div>

              </figure>
            </li>
          ))
        }

      </ul>
    </div>
  )
}
