import React, { useContext, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

import {searchBookDb} from './DB';
import { MyContext } from './Context';
//========== ========== ========== ========== ========== ========== ==========

//========== ========== ========== ========== ========== ========== ==========
export default function SearchCompo({oriPla}) {//oriPla
    let {items, setItems,   pageNum} = useContext(MyContext);



    //navigate Hook moveTo  
    const navigate = useNavigate(); 
    const moveTo = (url, items='') => {
        navigate(url, {state: items});
    }//moveTo() 

    useEffect(()=>{
       
        const elSearchForm = document.querySelector('.searchWrapper form.searchLeftContents'), //  
              elSearchInput = elSearchForm.search, //  input
              elSearchCheck = elSearchForm.titleOrAll; //  ()

        //  
        elSearchForm.onsubmit = async (e) => {
            e.preventDefault();
            setItems([]); //   

            // searchKeyAndToggle.current = [elSearchInput.value, elSearchCheck.checked];
            window.sessionStorage.setItem('searchValue', elSearchInput.value); 
            window.sessionStorage.setItem('toggleValue', elSearchCheck.checked); 

            let result = await searchBookDb(elSearchInput.value ,elSearchCheck.checked);
            console.log('result', result);
            
            
            if(result != 'err'){// result     
                pageNum.current = 0;
                
                let db = result.data;
                let dbItems = db.items;   

                if(dbItems.length != 0){
                
                    if(!oriPla){ //Home 
                        moveTo('/Search', dbItems);
                    }else{ //Search 
                        setItems([...dbItems])
                    }

                }else{
                    if(!oriPla){ //Home 
                        moveTo('/Search', 'NoResult');
                        console.log('1');
                    }else{ //Search 
                        setItems('NoResult');
                        console.log('2');
                    }
                }

            }else{// result   
                alert('  .')
            }
            
        };//elSearchForm.onsubmit

    },[]);



//---------- ---------- ---------- ---------- ----------
  return (
    <div className='searchWrapper'>

        <form className='searchLeftContents'>
            <div className='searchToggleWrapper'>{/*    */}
                <div className='searchToggle'>{/*   */}
                    <input type="checkbox" name="titleOrAll" id='tOrA'></input>
                    <label htmlFor="tOrA"></label>
                </div>
            </div>

            <input type='text' name='search' placeholder='  .'/>
            <button className='searchIcon' />

        </form>

    </div>
  )
}
