import { BrowserRouter, HashRouter, Link, Route, Routes } from 'react-router-dom';
import { useContext, useEffect, useRef } from 'react';
import ReactAudioPlayer from 'react-audio-player';
import KakaoLogin from "react-kakao-login";

import icon_note from '../img/icon_music.svg'; //
import icon_search from '../img/icon_search.svg'; //

import rainSound from '../sound/rainSound.mp3';
import waveSound from '../sound/waveSound.mp3';
import seagullSound from '../sound/seagullSound.mp3';
import firewoodSound from '../sound/firewoodSound.mp3';
import smallRiverSound from '../sound/smallRiverSound.mp3';
import birdSound from '../sound/birdSound.mp3';


import rainSoundImg from '../img/whiteNoise_rain.jpg';
import waveSoundImg from '../img/whiteNoise_wave.jpg';
import seagullSoundImg from '../img/whiteNoise_seagull.jpg';
import firewoodSoundImg from '../img/whiteNoise_firewood.jpg';
import smallRivalSoundImg from '../img/whiteNoise_smallRiver.jpg';
import birdSoundImg from '../img/whiteNoise_bird.jpg';

import { MyContext } from './Context';
import { loginObjectFun, saveLoginObject } from './DB';

export default function FixedCompo() {

  let { id1List, setId1List, loginObj, setLoginObj, loginBool, setLoginBool } = useContext(MyContext);

  // console.log('==============', loginObj, loginObj?.nickname);

  let elVerticalContent = useRef(null), 
      elVerticalContent_nicModify = useRef(null),
      elInput_nicModify = useRef(null);

      
  useEffect(() => {
    const elMenuBtn = document.querySelector('.headerContents button.menuBtn'),//
      elVerticalMenu = document.querySelector('.verticalMenu'),// 
      elmenuXBtn = document.querySelector('.verticalMenu button.menuXBtn'),// x
      elA_moveToList = elVerticalMenu.querySelector('.moveToList');// x

    const elSoundBtn = document.querySelector('footer .whiteNoise'),//  
      elSoundImg = document.querySelector('footer .whiteNoise img'),//   
      elWhiteNoiseUi = document.querySelector('footer .whiteNoiseUi'),// ui
      elSoundBtns = elVerticalMenu.querySelectorAll('ul.soundBtnWrapper > li.soundBtn');//  

    // ,  
    elMenuBtn.onclick = () => {
      elVerticalMenu.classList.add('active');
    }
    //x ,  
    elmenuXBtn.onclick = () => {
      elVerticalMenu.classList.remove('active');
    }

    //   ,  ui
    elSoundBtn.onclick = () => {
      elWhiteNoiseUi.classList.toggle('active');
    }

    elA_moveToList.onclick = () => {
      elVerticalMenu.classList.remove('active');
    };

    let preEle = elSoundBtns[5]; //  (elSoundBtns[5]) 
    elSoundBtns.forEach((ele, k) => {
      ele.onclick = (e) => {
        let soundLink = '';
        let imgLink = '';
        let err = false;
        switch (k) {
          case 0:
            soundLink = rainSound;
            imgLink = rainSoundImg;
            break;
          case 1:
            soundLink = waveSound;
            imgLink = waveSoundImg;
            break;
          case 2:
            soundLink = seagullSound;
            imgLink = seagullSoundImg;
            break;
          case 3:
            soundLink = firewoodSound;
            imgLink = firewoodSoundImg;
            break;
          case 4:
            soundLink = smallRiverSound;
            imgLink = smallRivalSoundImg;
            break;
          case 5:
            soundLink = birdSound;
            imgLink = birdSoundImg;
            break;
          default:
            console.log('  .');
            err = true;
        }

        if (!err) {
          elWhiteNoiseUi.setAttribute('src', soundLink);
          elSoundImg.setAttribute('src', imgLink);
        }

        preEle.classList.remove('active');
        e.target.classList.add('active');
        preEle = e.target;
      }
    });



  }, []);

  const kakaoLogin = () => {
    Kakao.init('be02ad515516537263f1362bbaeb2e5b'); //   javascript .
    console.log(Kakao.isInitialized()); // sdk


    Kakao.Auth.login({
      success: function (response) {
        Kakao.API.request({
          url: '/v2/user/me',
          success: async function (response) {
            console.log('', response);
            let myObj = await loginObjectFun(response); // obj
            console.log(' ', myObj.data);
            setLoginObj(myObj.data);
            setLoginBool(true);
          },
          fail: function (error) {
            console.log(error);
          },
        })
      },
      fail: function (error) {
        console.log(error);
      },
    })
  }//kakaoLogin() 

  const kakaoLogout = () => {

    if (Kakao.Auth.getAccessToken()) {
      Kakao.API.request({
        url: '/v1/user/unlink',
        success: function (response) {
          console.log('', response);
          setLoginObj('');
          setLoginBool(false);
        },
        fail: function (error) {
          console.log(error);
        },
      })
      Kakao.Auth.setAccessToken(undefined);
    }
  }//kakaoLogout 

  const nicknameModify = () => {
    elVerticalContent.current.classList.add('nicnameModify');
    elVerticalContent_nicModify.current.classList.add('active');
  }//nicknameModify 

  const nicknameComeBack = () => {
    let userNickname = elInput_nicModify.current.value;
    if(userNickname.length <= 8){
    let userObj = loginObj;

      userObj.nickname = userNickname;

      elVerticalContent.current.classList.remove('nicnameModify');
      elVerticalContent_nicModify.current.classList.remove('active');
  
      console.log('userObj', userObj);
      setLoginObj(userObj);
    }else{
      alert(' 8  .');
    }


  }//nicknameModify 



  return (
    <>
      <header>
        <div className='headerContents'>
          <h1 className='logo'><Link to="/"> </Link></h1>
          <button type='button' className='menuBtn'></button>
        </div>
      </header>

      <footer>
        <ReactAudioPlayer
          className='whiteNoiseUi'
          src={birdSound}
          autoPlay
          controls
        />

        <Link to="/Search" className='search'>
          <img src={icon_search} alt=' _ ' />
        </Link>
        <div className='nickname' onClick={kakaoLogout} title={loginBool? '  .' : ' .'}>
          {loginBool ? loginObj?.nickname : ''}
        </div>
        <div className='whiteNoise' title=' UI .'>
          <div className='bg'>
            <img src={birdSoundImg} alt=' ' />
          </div>
          <img src={icon_note} alt=' ' />
        </div>
      </footer>





      <div className={loginBool? 'verticalMenu login' : 'verticalMenu'} >
        {/* x */}
        <button type='button' className='menuXBtn'></button>

        {/*  */}
        <div className='content' ref={elVerticalContent}>

          <div className='nicknameWrapper'>{/* {loginBool ? loginObj?.nickname : ''} */}
            <span className='nickname' title=' 8  .'>{loginBool ? loginObj?.nickname : loginObj?.nickname}</span>
            <button type='button' className='nicknameModify' onClick={nicknameModify}></button>
          </div>
          <ul className='soundBtnWrapper'>

            <li className='soundBtn'>
              <div className='soundMark'>
                <img src={icon_note} alt='' />
              </div>
            </li>

            <li className='soundBtn'>
              <div className='soundMark'>
                <img src={icon_note} alt='' />
              </div>
            </li>

            <li className='soundBtn'>
              <div className='soundMark'>
                <img src={icon_note} alt='' />
              </div>
            </li>

            <li className='soundBtn'>
              <div className='soundMark'>
                <img src={icon_note} alt='' />
              </div>
            </li>

            <li className='soundBtn'>
              <div className='soundMark'>
                <img src={icon_note} alt='' />
              </div>
            </li>

            <li className='soundBtn active'>
              <div className='soundMark'>
                <img src={icon_note} alt='' />
              </div>
            </li>

          </ul>

          <div className='MyBookListWrapper'>
            <Link className='moveToList' to="/MyList" >
              My Book List
            </Link>

            <ul className='MyBookList'>
              {
                id1List.slice(0, 3).map((obj, k) => (
                  <li key={k}><span>{obj.title}</span></li>
                ))
              }
            </ul>
          </div>

        </div>{/* content */}

        <div className='content_nicnameModify' ref={elVerticalContent_nicModify}>

          <div className='nicknameWrapper'>
            <input className='nickname' placeholder='  .' ref={elInput_nicModify}></input>
            <button type='button' className='nicknameModify' onClick={nicknameComeBack}></button>
          </div>

        </div>{/* content NicnameModify */}

        <div className='loginWrapper'>

          {/* <div className='loginGoogle'>
            <div className='left'>
              <p></p>
            </div>
             
          </div> */}

          <div className='loginKakao' onClick={kakaoLogin}>
            <div className='left'>
              <p></p>
            </div>
             
          </div>

        </div>{/* loginWrapper */}

        <span className='logoutInfo'>    .</span>
      </div>
    </>
  )
}
