import { useContext, useEffect } from 'react'
import { useLocation, useNavigate } from 'react-router-dom';

//---------- ---------- ---------- ---------- ----------

import list from '../db/MyBookList.json';
import { MyContext } from './Context';
import { searchBookDb } from './DB';

import SearchCompo from './SearchCompo';

import NoResultImg from '../img/logo_BookReview2_noSearch_green.svg'

//========== ========== ========== ========== ==========

export default function Search() {
  let {items, setItems,   pageNum,   id1List, setId1List} = useContext(MyContext);

  const location = useLocation(),
        {state} = location; //Home SearchCompo.js items   
  
  //navigate Hook moveTo  
  const navigate = useNavigate(); 
  const moveTo = (url) => {
    navigate(url);
  }//moveTo() 

  //items( ) , '' 'none' 'inline-block' 
  let itemsBoolean = items.length && items != 'NoResult' && 9 <= items.length ? 'inline-block' : 'none';
                                                            //:  9 
  useEffect(()=>{
     //   items 

    if(state && state != 'NoResult'){//state   
      setItems([...state]);
    }else if(state == 'NoResult'){
      setItems(state);
    }

    const elSeeMoreBtn = document.querySelector('.bookWrapper button.seeMoreBtn');
    
    
    return () => {
      setItems([]);
    }
  },[])
  
  // 
  const SeeMoreBtn = async () => {
    
    pageNum.current += 1;
    let searchAndToggle = [window.sessionStorage.getItem('searchValue'), window.sessionStorage.getItem('toggleValue')];
    console.log('aaaaaaaaaaaaaaaaaa', searchAndToggle, pageNum);
    let a = await searchBookDb(searchAndToggle[0] ,searchAndToggle[1], (pageNum.current*10));

    let db = a.data; 
    let dbitems = db.items;   

    
    if(items != 'NoResult'){
      console.log(items,'---------------------')
      console.log('items, dbitems', items, dbitems);
      setItems([...items, ...dbitems]);
    }

  }//elSeeMoreBtn.onclick //

  const titleFun = (oriTitle='') => {
    let end = oriTitle.indexOf('(');
  
    if(end <= -1){
      return oriTitle;
    }
    return oriTitle.slice(0, end);
  }

  const changeMultiWord = (oriStr, aArr, bArr) => {
    //       .
    let changeStr = oriStr;// 

    for (let i = 0; i < aArr.length; i++) {
      let RE = new RegExp(aArr[i], "g");
      changeStr = changeStr.replace(RE, bArr[i]);
    }

    return changeStr;
  }//changeMultiWord(oriStr, aArr, bArr) 


  const heartClick = (e, itemObj) => {
    e.stopPropagation();
    let exist = false;

    if(itemObj != undefined){
      id1List.map((obj)=>{
        if(obj.isbn == itemObj.isbn ){exist = true;}
      })//id1List.map

      if(!exist){
        e.target.classList.add('active');

        let obj = {
          "title":  titleFun(itemObj.title),
          "author":  itemObj.author,
          "publisher":  itemObj.publisher,
          "img":  itemObj.image,
          "isbn":  itemObj.isbn,
        };
        
        setId1List([obj, ...id1List]);
      }else{
        e.target.classList.remove('active');

        let imsiList = id1List.filter((obj)=>{
          if(obj.isbn != itemObj.isbn) return obj
        })
        setId1List(imsiList);
      }//else

    }//if(itemObj != undefined)

  }//heartClick() 

  const existList = (itemObjId) => {
    let bool = false;

    if(itemObjId != undefined){
    id1List.map((obj)=>{

      if(obj.isbn == itemObjId ){bool = true;}
    })//id1List.map

    }

    return bool;
  }//existList() 

//---------- ---------- ---------- ---------- ----------
  return (
    <div className='SearchCompo'>
    
      <SearchCompo oriPla={true}/>

      <div className='bookWrapper'>
        <ul className='bookItemWrapper'>
          
           {
            items != 'NoResult' ? (
            items.map((obj, k) => (                                    //id  isbn 
              <li key={k} className='bookItem' onClick={() => {moveTo(`/Detail/${obj.isbn}`)}}>
                <figure>
                  <i>
                    <img src={obj?.image} alt={obj?.title}/>
                    <div className={existList(obj.isbn) ? 'heart active' : 'heart'} onClick={(e) => {heartClick(e, obj)}}/>
                  </i>

                  <figcaption>{titleFun(obj?.title)}</figcaption>
                  <span className='author'>: <code>{changeMultiWord(obj?.author, ['\\^'], [', '])}</code></span>
                  <span className='publisher'>: <code>{obj?.publisher}</code></span>
                </figure>
              </li> 
            ))

            ): <li className='noResult'><img src={NoResultImg} alt=' .'/></li>
          } 

        </ul>

          <button className='seeMoreBtn' style={{'display': itemsBoolean}} onClick={SeeMoreBtn}></button>
        
      </div>


    </div>
  )
}
