import React, { useEffect, useRef, useState } from 'react'
import { useParams } from 'react-router-dom';
import { Swiper, SwiperSlide } from 'swiper/react';

import {YoutubeTimeDb, searchBlogDb, searchIsbnBookDb, searchYoutubeDb} from './DB';
//========== ========== ========== ========== ========== ========== ==========

const formatDate = (inputDate) => {
    //    'YYYY-MM-DD'  
    if(inputDate){
        const year = inputDate.slice(0, 4);
        const month = inputDate.slice(4, 6);
        const day = inputDate.slice(6, 8);
        return `${year}-${month}-${day}`;
    }
}

//a 
const moveToUrl = (url) => {
  // window.location.href = url; //    
  window.open(url, '_blank'); //   
}//moveToUrl(url) 


const changeMultiWord = (oriStr, aArr, bArr) => {
  //       .
  let changeStr = oriStr;// 

  for (let i = 0; i < aArr.length; i++) {
    let RE = new RegExp(aArr[i], "g");
    changeStr = changeStr.replace(RE, bArr[i]);
  }

  return changeStr;
}//changeMultiWord(oriStr, aArr, bArr) 

const titleFun = (oriTitle='') => {
  let end = oriTitle.indexOf('(');

  if(end <= -1){
    return oriTitle;
  }
  return oriTitle.slice(0, end);
}

const titleSubFun = (oriTitle='') => {
  let start = oriTitle.indexOf('(');
  let end = oriTitle.indexOf(')');

  if(end <= -1){
    return '';
  }
  return oriTitle.slice(start + 1, end);
}


//========== ========== ========== ========== ========== ========== ==========
export default function Detail() {
    let {id} = useParams();
    let [bookObj, setBookObj] = useState();
    let [blogObj, setBlogObj] = useState([]);
    let [youtubeObj, setYoutubeObj] = useState([]);
    
    
    let activeYoutubeFun; let inActiveYoutubeFun;
    let elYoutubeVideoDiv = useRef();
    let elYoutubeVideo = useRef();
    useEffect(()=>{
      // elYoutubeVideo = useRef(''); 
      
        const searchIsbnFun = async () => {
            let imsi1 = await searchIsbnBookDb(id);
            // console.log('', imsi1);
            
            let imsi2 = await searchBlogDb(imsi1.data.items[0].title+' '+imsi1.data.items[0].author);
            // console.log('imsi2',imsi2); 
            
            let imsi3 = await searchYoutubeDb(imsi1.data.items[0].title);
            console.log('imsi3',imsi3); 
            
            setBookObj(imsi1.data.items[0]); 
            setBlogObj(imsi2.data.items);
            setYoutubeObj(imsi3.data.items);
            
            
            
          }//searchIsbnFun
          searchIsbnFun();
          
        
          
          const elAcc = document.querySelector('.descriptionAccordion'),
          elAccH3 = elAcc.querySelector('h3');
          
          elAccH3.onclick = ()=>{
            elAcc.classList.toggle('active');
          }
          
        },[]);
        
        // const getYoutubeTime = async (videoId) => {
        //   console.log('',videoId);
        //   let time = await YoutubeTimeDb(videoId);
        //   return time;
        // }

      const changeMultiWord = (oriStr, aArr, bArr) => {
          //       .
          let changeStr = oriStr;// 
  
          for (let i = 0; i < aArr.length; i++) {
            let RE = new RegExp(aArr[i], "g");
            changeStr = changeStr.replace(RE, bArr[i]);
          }
  
          return changeStr;
      }//changeMultiWord(oriStr, aArr, bArr) 

      activeYoutubeFun = (key) => {
        
        elYoutubeVideo.current.setAttribute('src', `https://www.youtube.com/embed/${key}`);
        elYoutubeVideoDiv.current.classList.add('active');
      }
      
      inActiveYoutubeFun = () => {
        console.log(' ?', elYoutubeVideo);
        elYoutubeVideo.current.setAttribute('src', '');
        elYoutubeVideoDiv.current.classList.remove('active');
      }

      const YoutubeTime = async (id) => {
        console.log('ididid1', id)
        let time = await YoutubeTimeDb(id);
        console.log('ididid2', time)

        return time;
      }
      
      
      return (
        <div className='DetailCompo'>
          <figure className='bookInfoWrapper'>
            <i>
              <img src={bookObj?.image} alt=''/>{/* bookObj?.image */}
            </i>

            <div className='right'>
              <figcaption>{titleFun(bookObj?.title)}</figcaption>{/* {bookObj?.title.length > 20 ? '...' : ''} */}
              <span>{titleSubFun(bookObj?.title)}</span>

              <ul>
                <li className='author'>: <code>{bookObj ? changeMultiWord(bookObj?.author, ['\\^'], [', ']) : ''}</code></li>
                <li className='publisher'>: <code>{bookObj?.publisher}</code></li>
              </ul>

              <div className='priceWrapper'>
                <strong>{Number(bookObj?.discount).toLocaleString('en')}</strong>
                <a href={bookObj?.link} target='_blank'> </a>
              </div>

              <div className='AddedInfo'>
                <span className='ISBN'>ISBN: <code>{bookObj?.isbn}</code></span>
                <span className='Date'><code>{formatDate(bookObj?.pubdate)}</code></span>{/* 2023-09-01 */}
              </div>
            </div>

          </figure>

          <div className='descriptionAccordion'>
            <h3></h3>
            <div className='description'>
                {bookObj ? changeMultiWord(bookObj?.description, ['&lt;', '&gt;'], ['<', '>']) : ''}{/* changeMultiWord(bookObj?.description, ['&lt;', &gt;], ['<', '>']) // bookObj?.description*/}
            </div>
          </div>

          <article className='popularBlogReviewWrapper'>
              <h2> </h2>
              <Swiper className='popularBlogReviewSwiper'
              slidesPerView={1.5}
              centeredSlides={true}
              spaceBetween={30}
              loop={true}
              breakpoints={{
                550: {
                  slidesPerView: 1.5,
                },
                600: {
                  slidesPerView: 2,
                }
              }} 
              >
                {
                    blogObj.map((obj, k)=>(
                        <SwiperSlide key={k} onClick={() => {moveToUrl(obj.link)}}>
                            <div className='content'>
                                <strong>
                                    {changeMultiWord(obj.title, ['<b>', '</b>', '&gt;', '&lt;', '&quot;'], ['', '', '>', '<', '"'])}  
                                </strong>
                                <b>{obj.bloggername}</b>
                                <p>
                                    {changeMultiWord(obj.description, ['<b>', '</b>', '&gt;', '&lt;', '&quot;'], ['', '', '>', '<', '"'])}
                                </p>
                            </div>
                            <span className='itmeNumber'>{k+1}</span>
                        </SwiperSlide>
                    ))
                }

                
                  
              </Swiper>
          </article>

          <article className='popularYoutubeReviewWrapper'>
              <h2>  </h2>
              <Swiper className='popularYoutubeReviewSwiper'
              slidesPerView={1.5}
              centeredSlides={true}
              spaceBetween={30}
              loop={true}
              breakpoints={{
                550: {
                  slidesPerView: 1.5,
                },
                600: {
                  slidesPerView: 2,
                }
              }} 
              >
                {
                    youtubeObj.map((obj, k)=>(
                        <SwiperSlide key={k} onClick={() => {activeYoutubeFun(obj.id.videoId)}} style={{'background': `URL(${obj.snippet.thumbnails.medium.url}) center center / cover no-repeat scroll`}}>
                            <div className='infoBg'>
                                <img className='playBtn' src='./icon_youtubePlay.svg' alt='playBtn'/>
                                <span className='durationTime'>3:11</span>{/* {YoutubeTime(obj.id.videoId)} */}
                            </div>
                            <span className='itmeNumber'>{k+1}</span>
                        </SwiperSlide>
                    ))
                }
              </Swiper>

              <div className='YoutubeVideo' ref={elYoutubeVideoDiv}>
                    <button type='button' className='xBtn' onClick={inActiveYoutubeFun}></button>
                    <iframe className='video'
                        src=''
                        title="video"
                        ref={elYoutubeVideo}
                    >
                          iframe  .
                    </iframe>
              </div>

          </article>

    </div>
  )
}
