import {createContext, useState, useRef, useEffect} from 'react'
import list from '../db/MyBookList.json';
import axios from 'axios';
import { saveLoginObject } from './DB';
//========== ========== ========== ========== ==========

export const MyContext = createContext(null);

//========== ========== ========== ========== ==========
export default function Context({children}) {

  const [items, setItems] = useState([]);//   
  // const [items, setItems] = useState([{title: ' '}]);//    
  const pageNum = useRef(0);//    pageNum
  const [id1List, setId1List] = useState(list.id1); // id1  id(isbn)
  const [loginObj, setLoginObj] = useState();
  const [loginBool, setLoginBool] = useState(false);

  const mounted = useRef(false);

  // useEffect(()=>{
  //   setLoginBool(window.sessionStorage.getItem('LoginBool'));

  // },[window.sessionStorage.getItem('LoginBool')])

  useEffect(() => {
    if(!mounted.current){
      mounted.current = true;
    }else{
      if(loginObj){
        let imsiObj = loginObj;
        imsiObj.bookList = id1List;
        saveLoginObject(imsiObj);
      }
    }
  }, [id1List])

  useEffect(()=>{
    if(mounted.current){
      if(loginBool){
        console.log('loginObj', loginObj)
        setId1List(loginObj.bookList);
      }
    }
  }, [loginBool])

  useEffect(()=>{
    if(mounted.current){
      if(loginBool && loginObj){
        setLoginBool(loginObj);
      }
    }
  },[loginObj])
//---------- ---------- ---------- ---------- ----------





  return (
    <MyContext.Provider value={
      {items, setItems,   
      pageNum,   
      id1List, setId1List,  
      loginObj, setLoginObj,
      loginBool, setLoginBool}}>
        
      {children}
    </MyContext.Provider>
  )
}
